import React, { useEffect, useState, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { connect } from "./redux/blockchain/blockchainActions";
import { fetchData } from "./redux/data/dataActions";
import * as s from "./styles/globalStyles";
import styled from "styled-components";

const truncate = (input, len) =>
  input.length > len ? `${input.substring(0, len)}...` : input;

export const StyledButton = styled.button`
  padding: 10px;
  border-radius: 12px;
  background-color: var(--primary);
  border: 4px solid var(--secondary);
  padding: 16px;
  font-size: 32px;
  font-family: mat_salehregular;
  color: var(--primary-text);
  width: 100%;
  cursor: pointer;
  box-shadow: 6px 6px var(--secondary);
`;

export const StyledRoundButton = styled.button`
  padding: 10px;
  border-radius: 100px;
  background-color: var(--primary);
  border: 4px solid var(--secondary);
  padding: 0px 0px 0px 0px;
  font-weight: bold;
  font-size: 32px;
  color: var(--primary-text);
  width: 12px;
  height: 12px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  }
`;

export const ResponsiveWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: stretched;
  align-items: stretched;
  width: 100%;
  @media (min-width: 67px) {
    flex-direction: row;
  }
`;

export const StyledLogo = styled.img`
  width: 300px;
  @media (min-width: 767px) {
    width: 700px;
  }
  transition: width 0.5s;
  transition: height 0.5s;
`;

export const StyledLogoFooter = styled.img`
  width: 200px;
  @media (min-width: 767px) {
    width: 240x;
  }
  transition: width 0.5s;
  transition: height 0.5s;
`;

export const StyledImg = styled.img`
  box-shadow: 0px 5px 11px 2px rgba(0, 0, 0, 0.7);
  border: 4px dashed var(--secondary);
  background-color: var(--accent);
  border-radius: 100%;
  width: 200px;
  @media (min-width: 900px) {
    width: 250px;
  }
  @media (min-width: 1000px) {
    width: 300px;
  }
  transition: width 0.5s;
`;

export const StyledLink = styled.a`
  color: var(--secondary);
  text-decoration: none;
`;

function App() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const data = useSelector((state) => state.data);
  const [claimingNft, setClaimingNft] = useState(false);
  const [feedback, setFeedback] = useState(`CLICK BUY TO MINT YOUR NFT`);
  const [mintAmount, setMintAmount] = useState(1);
  const [CONFIG, SET_CONFIG] = useState({
    CONTRACT_ADDRESS: "",
    SCAN_LINK: "",
    NETWORK: {
      NAME: "",
      SYMBOL: "",
      ID: 0,
    },
    NFT_NAME: "",
    SYMBOL: "",
    MAX_SUPPLY: 1,
    WEI_COST: 0,
    DISPLAY_COST: 0,
    GAS_LIMIT: 0,
    MARKETPLACE: "",
    MARKETPLACE_LINK: "",
    SHOW_BACKGROUND: false,
  });

  const claimNFTs = () => {
    let cost = CONFIG.WEI_COST;
    let gasLimit = CONFIG.GAS_LIMIT;
    let totalCostWei = String(cost * mintAmount);
    let totalGasLimit = String(gasLimit * mintAmount);
    console.log("Cost: ", totalCostWei);
    console.log("Gas limit: ", totalGasLimit);
    setFeedback(`MINTING YOUR ${CONFIG.NFT_NAME}...`);
    setClaimingNft(true);
    blockchain.smartContract.methods
      .mint(mintAmount)
      .send({
        gasLimit: String(totalGasLimit),
        to: CONFIG.CONTRACT_ADDRESS,
        from: blockchain.account,
        value: totalCostWei,
      })
      .once("error", (err) => {
        console.log(err);
        setFeedback("SORRY, SOMETHING WENT WRONG PLEASE TRY AGAIN LATER");
        setClaimingNft(false);
      })
      .then((receipt) => {
        console.log(receipt);
        setFeedback(
          `WOW, THE ${CONFIG.NFT_NAME} IS YOUR! GO VISIT OPENSEA.IO TO VIEW IT.`
        );
        setClaimingNft(false);
        dispatch(fetchData(blockchain.account));
      });
  };

  const decrementMintAmount = () => {
    let newMintAmount = mintAmount - 1;
    if (newMintAmount < 1) {
      newMintAmount = 1;
    }
    setMintAmount(newMintAmount);
  };

  const incrementMintAmount = () => {
    let newMintAmount = mintAmount + 1;
    if (newMintAmount > 20) {
      newMintAmount = 20;
    }
    setMintAmount(newMintAmount);
  };

  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };

  const getConfig = async () => {
    const configResponse = await fetch("/config/config.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const config = await configResponse.json();
    SET_CONFIG(config);
  };

  useEffect(() => {
    getConfig();
  }, []);

  useEffect(() => {
    getData();
  }, [blockchain.account]);

  return (
    <s.Screen>
      <s.Container
        flex={1}
        ai={"center"}
        style={{ padding: "90px 0px 0px 0px", backgroundColor: "var(--primary)" }}
        image={CONFIG.SHOW_BACKGROUND ? "/config/images/bg.png" : null}
      >

        <ResponsiveWrapper flex={1} style={{marginBottom:160}}>

        <s.Container
          flex={4}
          jc={"center"}
          ai={"center"}
          style={{
            background: "",
            height: "200px"
            
          }}
        >
          <StyledLogo alt={"logo"} src={"/config/images/logo.png"} />
        </s.Container>

        </ResponsiveWrapper>

        <s.SpacerMedium />
        
        <ResponsiveWrapper flex={1} style={{ padding: 14, background: "var(--secondary)" }} test>

          <s.Container flex={1}></s.Container>

          <s.Container
            flex={2}
            jc={"center"}
            ai={"center"}
            style={{
              backgroundColor: "var(--accent)",
              padding: 20,
              borderRadius: 24,
              marginTop: "-100px",
              border: "5px solid var(--primary)",
              boxShadow: "0px 0px var(--primary)",
            }}
          >
            <s.TextTitle 
              style={{
                textAlign: "center",
                color: "var(--primary-text)",
                fontSize: 60
              }}>
                GET YOUR Lil Billie Now!
            </s.TextTitle>

            <s.SpacerMedium/>

            <s.TextSubTitle
              style={{
                textAlign: "center",
                color: "var(--primary-text)",
              }}>
                Little Billionaires Club is a hand-drawn collection of 10,000 NFTs on the Polygon network.
                 Minted Little Billionaires will be revealed once you get them
            </s.TextSubTitle>

            <s.SpacerLarge/><s.SpacerLarge/>

            <s.TextSubTitle
              style={{
                textAlign: "center",
                fontSize: 44,
                color: "var(--primary-text)",
              }}
            >
             Minted : {data.totalSupply}  /  {CONFIG.MAX_SUPPLY} 
            </s.TextSubTitle>

            <s.TextSubTitle
              style={{
                textAlign: "center",
                fontSize: 22,
                color: "var(--primary-text)",
              }}
            >
              1 {CONFIG.SYMBOL} Costs {CONFIG.DISPLAY_COST}{" "}
                  {CONFIG.NETWORK.SYMBOL} 
            </s.TextSubTitle>

            <s.SpacerSmall />
            {Number(data.totalSupply) >= CONFIG.MAX_SUPPLY ? (
              <>
                <s.TextTitle
                  style={{ textAlign: "center", color: "var(--primary-text)" }}
                >
                  THE SALE HAS ENDED
                </s.TextTitle>
                <s.TextDescription
                  style={{ textAlign: "center", color: "var(--primary-text)" }}
                >
                  YOU CAN STILL FIND {CONFIG.NFT_NAME} ON
                </s.TextDescription>
                <s.SpacerSmall />
                <StyledLink target={"_blank"} href={CONFIG.MARKETPLACE_LINK}>
                  {CONFIG.MARKETPLACE}
                </StyledLink>
              </>
            ) : (
              <>
                <s.SpacerSmall />
                {blockchain.account === "" ||
                blockchain.smartContract === null ? (
                  <s.Container ai={"center"} jc={"center"}>
                    <s.TextDescription
                      style={{
                        textAlign: "center",
                        color: "var(--primary-text)",
                      }}
                    >
                      Connect to  {CONFIG.NETWORK.NAME} Network
                    </s.TextDescription>
                    <s.SpacerSmall />
                    <StyledButton
                      onClick={(e) => {
                        e.preventDefault();
                        dispatch(connect());
                        getData();
                      }}
                    >
                      Connect
                    </StyledButton>
                    {blockchain.errorMsg !== "" ? (
                      <>
                        <s.SpacerSmall />
                        <s.TextDescription
                          style={{
                            textAlign: "center",
                            color: "var(--primary-text)",
                          }}
                        >
                          {blockchain.errorMsg}
                        </s.TextDescription>
                      </>
                    ) : null}
                  </s.Container>
                ) : (
                  <>
                    <s.TextDescription
                      style={{
                        textAlign: "center",
                        color: "var(--primary-text)",
                      }}
                    >
                      {feedback}
                    </s.TextDescription>
                    <s.SpacerMedium />
                    <s.Container ai={"center"} jc={"center"} fd={"row"}>
                      <StyledRoundButton
                        style={{ lineHeight: 0.4 }}
                        disabled={claimingNft ? 1 : 0}
                        onClick={(e) => {
                          e.preventDefault();
                          decrementMintAmount();
                        }}
                      >
                        -
                      </StyledRoundButton>
                      <s.SpacerMedium />
                      <s.TextTitle
                        style={{
                          textAlign: "center",
                          color: "var(--primary-text)",
                          fontSize: 32,
                        }}
                      >
                        {mintAmount}
                      </s.TextTitle>
                      <s.SpacerMedium />
                      <StyledRoundButton
                        disabled={claimingNft ? 1 : 0}
                        onClick={(e) => {
                          e.preventDefault();
                          incrementMintAmount();
                        }}
                      >
                        +
                      </StyledRoundButton>
                    </s.Container>
                    <s.SpacerSmall />
                    <s.Container ai={"center"} jc={"center"} fd={"row"}>
                      <StyledButton
                        disabled={claimingNft ? 1 : 0}
                        onClick={(e) => {
                          e.preventDefault();
                          claimNFTs();
                          getData();
                        }}
                      >
                        {claimingNft ? "MINTING" : "BUY"}
                      </StyledButton>
                    </s.Container>
                  </>
                )}
              </>
            )}
            <s.SpacerMedium />
          </s.Container>

          <s.Container flex={1}></s.Container>

        </ResponsiveWrapper>

        <ResponsiveWrapper flex={1} style={{ background: "var(--secondary)" }}>

        <s.Container flex={1}></s.Container>

        <s.Container
          flex={2}
          jc={"left"}
          ai={"left"}
        >
          <s.SpacerLarge/><s.SpacerLarge/>

          <s.TextTitle
              style={{
                textAlign: "left",
                color: "var(--accent-text)",
                fontSize: 40,
                padding: "0px 24px"
              }}
            >
              NOTES !
            </s.TextTitle>

            <s.SpacerLarge/>
        </s.Container>

        <s.Container flex={1}></s.Container>

        </ResponsiveWrapper>

        <ResponsiveWrapper flex={1} style={{ background: "var(--secondary)" }}>

        <s.Container flex={1}></s.Container>

        <s.Container
          flex={2}
          jc={"left"}
          ai={"left"}
        >
       <s.TextSubTitle
              style={{
                textAlign: "left",
                color: "var(--accent-text)",
                padding: "0px 24px"
              }}>
              PLEASE MAKE SURE YOU ARE CONNECTED TO THE RIGHT NETWORK (
              {CONFIG.NETWORK.NAME} MAINNET) AND THE CORRECT ADDRESS. PLEASE NOTE:
              ONCE YOU MAKE THE PURCHASE, YOU CANNOT UNDO THIS ACTION.
              WE HAVE SET THE GAS LIMIT TO 285000 FOR THE CONTRACT SUCCESSFULLY MINT YOUR NFT.
              WE RECOMMEND THAT YOU DON'T LOWER THE GAS LIMIT
            </s.TextSubTitle>

            <s.SpacerLarge/>
        </s.Container>

        <s.Container flex={1}></s.Container>

        </ResponsiveWrapper>

        <ResponsiveWrapper flex={1} style={{ background: "var(--secondary)" }}>

        <s.Container flex={1}></s.Container>

        <s.Container
          flex={2}
          jc={"left"}
          ai={"left"}
        >
          <StyledLogoFooter alt={"logo"} style={{ padding: "0px 24px" }} src={"/config/images/logo.png"} />

          <s.SpacerXSmall/>

          <s.TextDescription
          style={{
            color: "var(--accent-text)",
            padding: "0px 4px"}}>
              Copyright Â© 2021 Lil Billionaires Club 
            <a
            style={{
              textDecoration: "none",
              color: "var(--primary-text)",
              backgroundColor: "var(--primary)",
              padding: "4px 8px",
              borderRadius: "4px",
              margin: "0px 4px 0px 8px",
            }}
            target={"_blank"} href={"https://twitter.com/LilbillieNft"}>Twitter
            </a>
            <a
            style={{
              textDecoration: "none",
              color: "var(--primary-text)",
              backgroundColor: "var(--primary)",
              padding: "4px 8px",
              margin: "0px 8px 0px 4px",
              borderRadius: "4px",
            }}
            target={"_blank"} href={"https://opensea.io/collection/lil-billionaires-club"}>OpenSea
            </a>
            <a
            style={{
              textDecoration: "none",
              color: "var(--primary-text)",
              backgroundColor: "var(--primary)",
              padding: "4px 8px",
              borderRadius: "4px",
            }}
            target={"_blank"} href={CONFIG.SCAN_LINK}>Smart Contract
            </a>
            <a
            style={{
              textDecoration: "none",
              color: "var(--primary-text)",
              backgroundColor: "var(--primary)",
              padding: "4px 8px",
              borderRadius: "4px",
              margin: "0px 4px 0px 8px",
            }}
            target={"_blank"} href={"https://littlebillie.github.io/config/roadmap.pdf"}>Roadmap
            </a>
          </s.TextDescription>

            <s.SpacerLarge/>
        </s.Container>

        <s.Container flex={1}></s.Container>

        </ResponsiveWrapper>
       

        

      </s.Container>
    </s.Screen>
  );
}

export default App;
